

















































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import Post from '@/post.ts';


class Tag {
  constructor(public name: string, public count: number) {}
}

class YearlyPost {
  constructor(public year: number, public count: number) {}
}

@Component
export default class BlogNav extends Vue {

  public get postTitle() {
    const post = this.findPost();

    if(post != null) {
      return post.title;
    }
    return "";
  }

  public get postTags() {
    const post = this.findPost();

    if(post != null) {
      return post.tags;
    }
    return "";
  }

  public get allTags() {
    const posts = Post.loadPosts();
    const results: Tag[] = [ ];
    
    posts.forEach(post => {
      post.tags.forEach(tagName => {
        const index = this.findIndex<Tag>(results, (t) => { return t.name == tagName; });

        if(index > -1) {
          results[index].count += 1;
        } else {
          results.push(new Tag(tagName, 1));
        }
      });
    });

    return results;
  }

  public get yearlyPosts() {
    const posts = Post.loadPosts();
    const results: YearlyPost[] = [];
    
    posts.forEach(post => {
      const index = this.findIndex<YearlyPost>(results, (yp) => { return yp.year == post.date.getFullYear(); });

      if(index > -1) {
        results[index].count += 1;
      } else {
        results.push(new YearlyPost(post.date.getFullYear(), 1));
      }
    });

    return results;
  }

  public copyLink() {
    navigator.clipboard.writeText(window.location.href);
  }

  private findIndex<T>(arr: T[], pred: (p: T) => boolean) {
    for (let i = 0; i < arr.length; i++) {
      const item = arr[i];
      if(pred(item)) {
        return i;
      }
    }
    return -1;
  }

  private findPost() {
    const posts: Post[] = Post.loadPosts();
    let post!: Post;

    const parts = this.$route.path.split('/');
    let yearPart = parts[2];
    
    if(this.$route.path == "/blog/") {
      yearPart = "latest";
    }
    console.log("Finding latest post...");

    if(yearPart == undefined || yearPart == "latest") {
      post = posts.sort((n1, n2) => {
        if(n1.date < n2.date) {
          return 1;
        } else if(n1.date > n2.date) {
          return -1;
        }
        return 0;
      })[0];
    } else {
      const safeTitle = parts[3];
      const year = parseInt(yearPart);
      post = posts.find(p => p.date.getUTCFullYear() == year && p.safeTitle == safeTitle) as Post;
    }

    return post;
  }

}
