import blogConfig from "@/blog.json";
export default class Post {

  public static posts: Post[] = [];

  constructor(public id: number, public title: string, public description: string, public leadingExcerpt: string, public tags: string[], public date: Date, public path: string, public safeTitle: string) {}

  static loadPosts() {
    if(this.posts.length > 0) {
      return this.posts;
    }

    const list: Post[] = [];

    // Can we use map here?
    blogConfig.blog.forEach(element => {
      // eslint-disable-next-line
      ///const safeTitle = element.title.replace(/^.*[\\\/]/, '').replace(".md", '').replace(".html", "").replace(" ", "_");
      list.push(new Post(element.id, element.title, element.description, element.leadingExcerpt, element.tags, new Date(element.date), element.path, element.safe_title));
    });

    this.posts = list;
    return list;
  }
}