import Vue from 'vue'
import App from '@/pages/index/index.vue'
import VueRouter from 'vue-router'
import VueTheMask from 'vue-the-mask';

import HomeContent from '@/components/HomeContent.vue';
import BlogArchive from '@/components/BlogArchive.vue';
import BlogPost from '@/components/BlogPost.vue';
import BlogNav from '@/components/BlogNav.vue';
import ContactForm from '@/components/ContactForm.vue';
import Post from '@/post.ts';
import ProjectsList from '@/components/ProjectsList.vue';


Vue.config.productionTip = false
Vue.use(VueTheMask);

const posts: Post[] = Post.loadPosts();

const latestPost = posts.sort((n1, n2) => {
  if(n1.date < n2.date) {
    return 1;
  } else if(n1.date > n2.date) {
    return -1;
  }
  return 0;
})[0];


const latestRedirect = `/blog/${latestPost.date.getUTCFullYear()}/${latestPost.safeTitle}/`
const routes = [
  {
    path: "/",
    name: "Moolick - Home",
    component: HomeContent,
  },
  {
    path: "",
    name: "Moolick - Home",
    component: HomeContent,
  },
  {
    path: '/projects/',
    component: ProjectsList,
    name: "Projects"
  },
  {
    path: '/blog/',
    redirect: '/blog/latest',
  },
  {
    path: '/blog/latest/',
    redirect: latestRedirect,
    name: latestPost.title,
    components: {
      default: BlogPost,
      side: BlogNav,
    },
  },
  {
    path: '/blog/archive/',
    components: {
      default: BlogArchive,
      side: BlogNav,
    },
  },
  {
    path: '/contact/',
    component: ContactForm
  },
]


const router = new VueRouter({
  mode: 'history',
  routes: routes,
});

posts.forEach(p => {
  console.log("Adding route for blogpost: " + `/blog/${p.date.getUTCFullYear()}/${p.safeTitle}/`)

  router.addRoutes([{
    path: `/blog/${p.date.getUTCFullYear()}/${p.safeTitle}/`,
    name: p.title,
    components: {
      default: BlogPost,
      side: BlogNav,
    },
  }])
});

Vue.use(VueRouter);


import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faClipboardList } from '@fortawesome/free-solid-svg-icons'
import { faBook } from '@fortawesome/free-solid-svg-icons'
import { faEnvelope } from '@fortawesome/free-solid-svg-icons'
import { faLink } from '@fortawesome/free-solid-svg-icons'
import { faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faMastodon } from '@fortawesome/free-brands-svg-icons'
import { faGithubAlt } from '@fortawesome/free-brands-svg-icons'
import { faComments } from '@fortawesome/free-regular-svg-icons'


library.add(faClipboardList)
library.add(faBook)
library.add(faEnvelope)
library.add(faLink)
library.add(faTwitter)
library.add(faMastodon)
library.add(faGithubAlt)
library.add(faComments)

Vue.component('font-awesome-icon', FontAwesomeIcon)


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')

const path = localStorage.getItem('path');
if(path) {
  console.log("Routing to '" + path + "'");
  localStorage.removeItem('path');
  router.push(path);
}