import projectConfig from "@/projects.json";
export default class Project {

  public static projects: Project[] = [];

  constructor(public title: string, public description: string, public tag: string, public url: string) {}

  static loadProjects() {
    if(this.projects.length > 0) {
      return this.projects;
    }

    const list: Project[] = [];

    projectConfig.projects.forEach(element => {
      list.push(new Project(element.title, element.description, element.tag, element.url));
    });

    this.projects = list;
    return list;
  }
}