










import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import Post from '@/post.ts';

import BlogDiscussion from '@/components/BlogDiscussion.vue';
import MarkdownItVue from 'markdown-it-vue';
import 'markdown-it-vue/dist/markdown-it-vue.css';

@Component({
  components: {
    MarkdownItVue,
    BlogDiscussion,
  }
})
export default class BlogPost extends Vue {

  @Prop()
  public content!: string;

  @Prop()
  public path!: string;

  @Prop()
  public tags!: string[];

  

  created() {
    this.loadContent();
  }

  loadContent() {
    this.content = "# *LOADING :hourglass_flowing_sand:*";
    const posts: Post[] = Post.loadPosts();
    let post!: Post;

    const parts = this.$route.path.split('/');
    let yearPart = parts[2];
    
    if(this.$route.path == "/blog/") {
      yearPart = "latest";
    }
    console.log("Finding latest post...");

    if(yearPart == undefined || yearPart == "latest") {
      post = posts.sort((n1, n2) => {
        if(n1.date < n2.date) {
          return 1;
        } else if(n1.date > n2.date) {
          return -1;
        }
        return 0;
      })[0];
    } else {
      const safeTitle = parts[3];
      const year = parseInt(yearPart);
      post = posts.find(p => p.date.getUTCFullYear() == year && p.safeTitle == safeTitle) as Post;
    }

    if (post == null) {
      this.content = "# Post not found"
    } else {
      document.title = post.title;
      this.path = post.path;
      this.tags = post.tags;

      console.log("Loading post " + post.path);
      this.loadMarkdown(post.path);
    }
  }

  loadMarkdown(filename: string) {
    const client = new XMLHttpRequest();
    client.open('GET', filename);
    
    client.onload = () => {
      if(client.readyState == 4 && client.status == 200) {
        this.content = client.responseText;
      }
    };
    client.onerror = () => {
      this.content = "**Post not found**"
    };
    client.send();
  }
}

