


















import { Component, Prop, Vue } from 'vue-property-decorator';
import Post from '@/post.ts';
import PostPreview from '@/components/PostPreview.vue';

@Component({
  components: {
    PostPreview,
  }
})
export default class BlogArchive extends Vue {

  get filtersText() {
    const targetYear = this.$route.query.y;
    const targetTag = this.$route.query.t;
    let results = "";

    if(targetYear != undefined) {
      results += `\n  Year: ${targetYear}`;

    }
    if(targetTag != undefined) {
      results += `\n  Tag: ${targetTag}`;
    }

    return results;
  }

  get years() {
    const posts: Post[] = Post.loadPosts();
    const results: number[] = [ ]

    const targetYear = this.$route.query.y;
    const targetTag = this.$route.query.t;

    for(let i = 0; i < posts.length; i++) {
      const value = posts[i];
      const year = value.date.getFullYear();

      if(targetYear != undefined) {
        const yearAsString = year.toString();
        if(targetYear != yearAsString) {
          continue;
        }
      }

      if(!results.includes(year)) {
        if(targetTag != undefined) {
          if(value.tags.includes(targetTag as string)) {
            results.push(year);
          }
        } else {
          results.push(year);
        }
      }
    }

    return results.sort((n1, n2) => {
      if(n1 < n2) {
        return 1;
      } else if(n1 > n2) {
        return -1;
      }
      return 0;
    });
  }

  yearlyPosts(target: number) {
    const posts: Post[] = Post.loadPosts();
    const results: Post[] = [ ];

    for(let i = 0; i < posts.length; i++) {
      const value = posts[i];
      const year = value.date.getFullYear();
      if(year == target) {
        if(this.$route.query.t != undefined) {
          if(value.tags.includes(this.$route.query.t as string)) {
            results.push(value);
          }
        } else {
          results.push(value);
        }
      }
    }


    return results;
  }

  formatDate(date: Date) {
    const options = { month: "long", day: "numeric", year: "numeric" };
    return new Intl.DateTimeFormat("default", options).format(date);
  }
}
